@use "../abstracts" as *;

html,
body {
  text-size-adjust: 100%;
}

html {
  font-size: 62.5%;
  // @include mq(lg) {
  //   font-size: get_vw(10);
  // }
  // @include mq(md) {
  //   font-size: 62.5%;
  // }
  // @include mq(xs) {
  //   font-size: get_vw(10, xs);
  // }
}

body {
  box-sizing: border-box;
  font-family: $font-base;
  font-size: 1.6rem;
  line-height: 2;
  color: $color-text-base;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  background-color: $color-bg-base;
  backface-visibility: hidden;
}

* {
  &,
  &:before,
  &:after {
    box-sizing: inherit;
  }
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
}

a {
  color: $color-text-link;
  text-decoration: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

picture {
  display: block;
}

table {
  border-collapse: collapse;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
dl {
  margin-bottom: 0;
  font-weight: normal;
}
