@use "sass:math";
@use "variables" as *;

// Media Queries
@mixin mq($canvas: md) {
  @each $size, $value in $mq {
    @if $canvas == min-#{$size} {
      @media not screen and (max-width: $value) {
        @content;
      }
    }
    @if $canvas == $size {
      @media only screen and (max-width: $value) {
        @content;
      }
    }
  }
}

// コンテンツ幅と余白を設定して中央寄せ
@mixin containerLayout() {
  max-width: $contentsWidth + $contentsPadding * 2;
  padding-right: $contentsPadding;
  padding-left: $contentsPadding;
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    padding-right: math.percentage(math.div(16, 375));
    padding-left: math.percentage(math.div(16, 375));
  }
}

// 親要素のタグを指定
@mixin unify-parent($child) {
  @at-root #{selector-unify(&, $child)} {
    @content;
  }
}
