@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes stretch {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}
html,
body {
  text-size-adjust: 100%;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "游ゴシック", "Yu Gothic", YuGothic, "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
  color: #000;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  background-color: #fff;
  backface-visibility: hidden;
}

*, *:before, *:after {
  box-sizing: inherit;
}
*:first-child {
  margin-top: 0;
}
*:last-child {
  margin-bottom: 0;
}

a {
  color: #06f;
  text-decoration: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

picture {
  display: block;
}

table {
  border-collapse: collapse;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
dl {
  margin-bottom: 0;
  font-weight: normal;
}

.container {
  max-width: 1380px;
  padding-right: 50px;
  padding-left: 50px;
  margin-right: auto;
  margin-left: auto;
}
@media only screen and (max-width: 768px) {
  .container {
    padding-right: 4.2666666667%;
    padding-left: 4.2666666667%;
  }
}

@media only screen and (max-width: 768px) {
  .-pc {
    display: none !important;
  }
}

.-sp {
  display: none !important;
}
@media only screen and (max-width: 768px) {
  .-sp {
    display: block !important;
  }
}

.-ib {
  display: inline-block;
}

.box-movie {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
}
.box-movie iframe {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.-target,
.-ready,
.-action {
  /* Class for scroll animation */
}

.l-header {
  padding: 16px 0;
  background-color: #eee;
}
.l-header .document-title {
  font-size: 30px;
}
@media only screen and (max-width: 768px) {
  .l-header .document-title {
    font-size: 34px;
  }
}

[class^=sec-],
[class*=" sec-"] {
  padding-top: 80px;
  padding-bottom: 80px;
}
@media only screen and (max-width: 768px) {
  [class^=sec-],
[class*=" sec-"] {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.document-lead-text {
  font-size: 2rem;
  color: #999;
}
@media only screen and (max-width: 768px) {
  .document-lead-text {
    font-weight: bold;
  }
}
@media only screen and (max-width: 768px) {
  .document-lead-text {
    font-size: 1.8rem;
  }
}