@use "../abstracts" as *;

.container {
  @include containerLayout();
}

.-pc {
  @include mq(md) {
    display: none !important;
  }
}

.-sp {
  display: none !important;
  @include mq(md) {
    display: block !important;
  }
}

.-ib {
  display: inline-block;
}

// ▼ You Tubeレスポンシブ対応用Box
.box-movie {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
  iframe {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }
}

.-target,
.-ready,
.-action {
  /* Class for scroll animation */
}
