@use "sass:map";

// Colors
$color-text-base: #000;
$color-bg-base: #fff;
$color-text-link: #06f;
$color-accent: #f00;

// Contents Width
$contentsWidth: 1280px;
$contentsPadding: 50px;

// Breakpoints
$mq: (
  "xs": 375px,
  "sm": 480px,
  "md": 768px,
  "lg": 1024px,
  "xl": 1440px,
);

// Fonts
$font-base: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "游ゴシック", "Yu Gothic", YuGothic, "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;

